import { CompileSubject, CompileRender, VM } from './compile';

export class Componet {
    readonly $name: string;
    readonly $parent: Componet;
    readonly $children: Array<Componet> = [];
    readonly $subObject: CompileSubject;
    //readonly $elements: Array<HTMLElement | Text> = [];
    readonly $parentElement: HTMLElement;

    /**
     * ViewViewComponet
     * @param p 
     */
    $update(p?: any) {
        if (this.$isDisposed) return;
        this.onUpdateBefore(() => {
            if (this.$isDisposed) return;
            this.$subObject.update({
                componet: this,
                param: p
            });
        }, p);
        this.onUpdate(function(){}, p);
    }

    private updateId:any;
    /**
     * ViewViewComponet
     * @param p 
     */
   $updateAsync(callback?:()=>void, p?:any){
       this.updateId && clearTimeout(this.updateId);
        this.updateId = setTimeout(() => {
            this.updateId = null;
            this.$update(p);
            callback && callback.apply(this);
        }, 5);
    }

    /**
     * CompileRender, include
     * (viewvar)p
     * @param tmpl 
     * @param p 
     */
    $render(tmpl: string, p?:Object): CompileRender {
        var rd = new CompileRender(tmpl, null, p);
        return rd;
    }

    /**
     * View
     * @param cb 
     * @param p 
     */
    onInit(cb: (err?: any) => void, p?: any): void {
        cb && cb();
    }

    /**
     * Viewvar, onInitonReady
     * @param cb 
     * @param p 
     */
    onInitViewvar(cb: (err?: any) => void, p?: any): void {
        cb && cb();
    }

    /**
     * View
     * @param cb 
     * @param p 
     */
    onReady(cb: (err?: any) => void, p?: any): void {
        cb && cb();
    }

    /**
     * $update
     * @param cb 
     */
    onUpdateBefore(cb: (err?: any) => void, p?: any): void {
        cb && cb();
    }

    /**
     * $update
     * @param cb 
     */
    onUpdate(cb: (err?: any) => void, p?: any): void {
        cb && cb();
    }

    /**
     * 
     */
    $isDisposed: Boolean = false;
    /**
     * componet
     */
    onDispose() {
    }

    constructor() {
    }
}
