import CmpxLib from './cmpxLib';

export default class CmpxEvent {
    private events: any[] = [];

    /**
     * 
     * @param fn 
     */
    on(fn: () => any) {
        this.events.push(fn);
    }

    /**
     * 
     * @param fn 
     */
    off(fn?: () => any) {
        if (fn) {
            let index = CmpxLib.inArray(this.events, fn);
            index >= 0 && this.events.splice(index, 1);
        } else
            this.events = [];
    }

    /**
     * , , false
     * @param args 
     * @param thisArg this
     */
    trigger(args: any[], thisArg: any): any {
        let ret: any;
        CmpxLib.each(this.events, function (item: any) {
            ret = item && this.apply(thisArg, args);
            return ret;
        });
        return ret;
    }
}